import React, { useState } from "react";
import styled from "styled-components";
import { FiBox, FiUser, FiSettings, FiBell, FiSearch } from "react-icons/fi";

const Container = styled.div`
  display: flex;
  flex-direction: column;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
`;

const Header = styled.header`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid #ddd;
`;

const NavMenu = styled.nav`
  display: flex;
  align-items: center;
`;

const NavItem = styled.div`
  margin: 0 15px;
  position: relative;
`;

const RightMenu = styled.div`
  display: flex;
  align-items: center;
`;

const IconButton = styled.button`
  background: none;
  border: none;
  margin: 0 10px;
  cursor: pointer;
`;

const SearchInput = styled.input`
  margin-right: 15px;
  padding: 5px 10px;
  border: 1px solid #ddd;
  border-radius: 4px;
`;

const SidebarContainer = styled.div`
  display: flex;
`;

const Sidebar = styled.div`
  width: 250px;
  padding: 20px;
  border-right: 1px solid #ddd;
  min-height: 100vh;
  position: relative;
`;

const Content = styled.div`
  flex-grow: 1;
  padding: 20px;
`;

const TreeView = styled.ul`
  list-style: none;
  padding: 0;
`;

const TreeNode = styled.li`
  margin: 5px 0;
  cursor: pointer;
`;

const ExpandCollapseLink = styled.span`
  color: blue;
  cursor: pointer;
  display: block;
  margin-top: 10px;
`;

const FolderList = styled.div`
  display: flex;
  flex-wrap: wrap;
  gap: 20px;
  margin-top: 20px;
`;

const FolderItem = styled.div`
  width: 150px;
  height: 150px;
  background-color: #ddd;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  text-align: center;
  cursor: pointer;
`;

const FolderIcon = styled.div`
  width: 60px;
  height: 60px;
  background-color: #aaa;
  border-radius: 5px;
  margin-bottom: 10px;
`;

const FolderLabel = styled.p`
  margin: 0;
  font-size: 14px;
`;

const ProjectPage = () => {
  const [expandedNodes, setExpandedNodes] = useState([]);

  const toggleNode = (node) => {
    setExpandedNodes((prevState) =>
      prevState.includes(node)
        ? prevState.filter((n) => n !== node)
        : [...prevState, node]
    );
  };

  const renderTree = (data) => {
    return data.map((item) => (
      <TreeNode key={item.name}>
        <div onClick={() => toggleNode(item.name)}>
          {expandedNodes.includes(item.name) ? "▼" : "►"} {item.name}{" "}
          {item.count && <span>({item.count})</span>}
        </div>
        {item.children && expandedNodes.includes(item.name) && (
          <TreeView>{renderTree(item.children)}</TreeView>
        )}
      </TreeNode>
    ));
  };

  const categories = [
    {
      name: "All Categories",
      children: [
        {
          name: "Tools",
          count: 11889,
          children: [
            { name: "Utilities", count: 2636 },
            { name: "Gui", count: 1475 },
            { name: "Integration", count: 1249 },
            { name: "Particles-Effects", count: 628 },
            { name: "Game-Toolkits", count: 616 },
            { name: "Physics", count: 513 },
            { name: "Input-Management", count: 484 },
            { name: "Modeling", count: 443 },
            { name: "Animation", count: 405 },
            { name: "Behavior-Ai", count: 390 },
            { name: "Terrain", count: 373 },
            { name: "Audio", count: 356 },
            { name: "Network", count: 333 },
            { name: "Level-Design", count: 319 },
            { name: "Camera", count: 309 },
            { name: "Sprite-Management", count: 233 },
            { name: "Visual-Scripting", count: 197 },
            { name: "Localization", count: 112 },
            { name: "Video", count: 100 },
            { name: "Painting", count: 89 },
          ],
        },
      ],
    },
  ];

  return (
    <Container>
      <Header>
        <NavMenu>
          <h1 style={{ marginRight: "30px" }}>Digital Asset Management</h1>
          <NavItem>Home</NavItem>
          <NavItem>Project</NavItem>
          <NavItem>My Workspace</NavItem>
        </NavMenu>
        <RightMenu>
          <SearchInput type="text" placeholder="Search..." />
          <IconButton>
            <FiSettings size={20} />
          </IconButton>
          <IconButton>
            <FiBell size={20} />
          </IconButton>
          <IconButton>
            <FiUser size={20} />
          </IconButton>
        </RightMenu>
      </Header>
      <SidebarContainer>
        <Sidebar>
          <h3>Project Name</h3>
          <TreeView>{renderTree(categories)}</TreeView>
          <ExpandCollapseLink onClick={() => toggleNode("Tools")}>
            + Show More
          </ExpandCollapseLink>
          <ExpandCollapseLink onClick={() => toggleNode("Tools")}>
            - Show Less
          </ExpandCollapseLink>
        </Sidebar>
        <Content>
          <FolderList>
            {["api_server", "app", "comfy", "comfy_extras", "comfy_execution", "comfyui_screens", "CODEOWNERS", "comfyui.log"].map((folder, index) => (
              <FolderItem key={index}>
                <FolderIcon />
                <FolderLabel>{folder}</FolderLabel>
              </FolderItem>
            ))}
          </FolderList>
        </Content>
      </SidebarContainer>
    </Container>
  );
};

export default ProjectPage;
